import React, { useState, useEffect } from 'react'
// import user info hoook to show available user info in the ui
import getUserInfo from '../hooks/getUserInfo'

const Dashboard = () => {
  // distructure the user info to be set in the info state
  const { userInfo } = getUserInfo()
  const [Info, setInfo] = useState([])

  // set info state and update page title effect
  useEffect(() => {
    if (userInfo != null) {
      setInfo(userInfo)
      document.title = Info.username + ' - AFD HUB'
    } else {
      setInfo([])
    }
  }, [userInfo, Info])

  return (
    <div className='bg-white shadow overflow-hidden rounded-lg mb-3'>
      <div className='px-4 py-5 sm:px-6'>
        <h3 className='text-lg leading-6 font-medium text-gray-900'>
          User Info
        </h3>
        <p className='mt-1 text-xs text-gray-500'>
          The user info is coiming from the user Api wihich is updated when the
          userAuth listener updates
        </p>
      </div>

      <dl>
        <div className='bg-gray-50 px-4 py-3 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6'>
          <dt className='text-sm font-medium text-gray-500'>Full name</dt>
          <dd className='mt-1 text-sm text-gray-900 sm:mt-0 sm:col-span-2'>
            {Info.firstName + ' ' + Info.lastName}
          </dd>
        </div>

        <div className='bg-white px-4 py-3 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6'>
          <dt className='text-sm font-medium text-gray-500'>Username</dt>
          <dd className='mt-1 text-sm text-gray-900 sm:mt-0 sm:col-span-2'>
            @{Info.username}
          </dd>
        </div>
        <div className='bg-gray-50 px-4 py-3 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6'>
          <dt className='text-sm font-medium text-gray-500'>Email Address</dt>
          <dd className='mt-1 text-sm text-gray-900 sm:mt-0 sm:col-span-2'>
            {Info.email}
          </dd>
        </div>
        <div className='bg-white px-4 py-3 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6'>
          <dt className='text-sm font-medium text-gray-500'>Phone Number</dt>
          <dd className='mt-1 text-sm text-gray-900 sm:mt-0 sm:col-span-2'>
            {Info.phone}
          </dd>
        </div>
      </dl>
    </div>
  )
}

export default Dashboard
